import { Link } from 'react-router-dom'

import style from '../styles/AuthFormElements.module.css'

import logo from '../assets/auth_images/logo.svg'
import authBg from '../assets/auth_images/auth_img.png'

export const AuthHeader = () => {
  return (
    <header className={style.header}>
      <Link className={style.logo} to="/home" replace={true}>
        <img src={logo} alt="zuri logo" />
      </Link>
    </header>
  )
}

export const AuthFooter = () => {
  return (
    <footer className={style.footer}>
      <Link className={style.contact} to="/contact-us" replace={true}>
        <small>Contact Us</small>
      </Link>
      <Link className={style.policy} to="/" replace={true}>
        <small>Legal Policy</small>
      </Link>
    </footer>
  )
}

export const AuthSideBar = () => {
  return (
    <aside className={`${style.authAsideContainer} ${style.display_none}`}>
      <div className={style.authImageWrapper}>
        <img src={authBg} alt="backgroundImage" />
        <div className={style.aside_txt}>
          <h4>Have fun while working</h4>
          <h4>as a team</h4>
          <p>Unlimited team collaboration platform</p>
        </div>
      </div>
    </aside>
  )
}

export const AuthFormWrapper = ({ children, submitHandler }) => {
  return <form onSubmit={submitHandler}>{children}</form>
}
